export function getIdKeyForGroupBy(groupBy = {}) {
    if (groupBy.account) {
        return 'account';
    }
    if (groupBy.project) {
        return 'project';
    }
    if (groupBy.region) {
        return 'region';
    }
    if (groupBy.service) {
        return 'service';
    }
    return 'date';
}
